import React, { useState ,useEffect} from 'react';
import "bootstrap/dist/css/bootstrap.min.css";
import './App.css';
import Movielist from './components/movielist';
import Heading from './components/movieheading';
import SearchBox from './components/searchbox';
import AddFavourites from "./components/AddFav";

const App = () => {
  const [movies, setMovies] = useState([]);
  const [searchv,setsearchv] = useState("");

  const getMovieRequest = async (searchv) => {
     const url = `http://www.omdbapi.com/?s=${searchv}&apikey=762bc0c5`;
     const response = await fetch(url);
     const responseJson = await response.json();
     if(responseJson.Search){
      setMovies(responseJson.Search);
     }
  };
 
  useEffect (() =>{
    getMovieRequest(searchv)
  },[searchv]);

  return( 
  <div className="container-fluid movie-app">
    <div className="row title d-flex aling-item-center mt-4 mb-4">
      <Heading heading="MovFlix" />
      <SearchBox searchv={searchv} setsearchv={setsearchv} />
    </div>
    <div className='row'>
				<Movielist
					movies={movies}
					favouriteComponent={AddFavourites}	
				/>
			</div>
    
  </div>);
};

export default App;
